nav {
  background: #e2e2e2;
  height: 4rem;
  margin-bottom: 1rem;
  position: static;

  .container {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    padding: 0 1.5rem;
    display: flex;
    align-items: center;

    div {
      display: flex;
      align-items: center;
      flex-grow: 1;

      svg {
        width: 2rem;
        height: 2rem;
        margin-right: 0.3rem;
        color: #0b60da;
      }
    }

    h1 {
      font-size: 1.5rem;
      color: #525252;
    }
  }
}
